@import url(https://fonts.googleapis.com/css?family=Annie+Use+Your+Telescope);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column; }

#root {
  background-color: #1a1a1a; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.container {
  background-color: #c0b9b1; }

.container--main {
  max-width: 700px;
  height: 100vh;
  -webkit-flex-direction: column;
          flex-direction: column;
  display: -webkit-flex;
  display: flex; }

.container--list {
  background-color: white;
  border-radius: 6px;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin-bottom: 100px;
  overflow: auto; }

.container--login {
  max-width: 700px;
  height: 100vh;
  background-image: url(/happytenants/static/media/background.765ab131.jpg);
  background-position: 50%; }

.container--login_h1 {
  font-family: 'Annie Use Your Telescope', cursive;
  color: #f8f8f8;
  font-size: 50px; }

.row--login {
  height: 100vh; }

.col--login {
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  min-height: 127px; }

.logo {
  display: block;
  margin: 0 auto;
  max-width: 300px;
  max-height: 300px;
  padding-right: 40px; }

.header {
  background-color: blueviolet;
  color: white;
  font-size: 2.5rem; }

.fontAwesomeIcon {
  color: blueviolet; }

.p--nav {
  color: blueviolet; }

.optionText {
  word-wrap: break-word; }

a:hover {
  text-decoration: none; }

.endRow {
  background-color: #f8f8f8;
  width: 100%;
  position: fixed;
  bottom: 0; }

.vl {
  border-left: 2px solid rgba(0, 0, 0, 0.123);
  position: relative;
  height: 60px;
  left: 50%;
  margin-left: -3px;
  top: 0; }

@media only screen and (min-width: 700px) {
  .endRow {
    background-color: #f8f8f8;
    width: 700px; } }

